<h2 id="一、生成dump文件" tabindex="-1"><a class="header-anchor" href="#一、生成dump文件" aria-hidden="true">#</a> 一、生成dump文件</h2>
<h3 id="出现oom时自动生成堆dump" tabindex="-1"><a class="header-anchor" href="#出现oom时自动生成堆dump" aria-hidden="true">#</a> 出现OOM时自动生成堆dump</h3>
<p>JVM启动命令增加两个参数:
-XX:+HeapDumpOnOutOfMemoryError
-XX:HeapDumpPath=/home/app/dumps/</p>
<h3 id="人工通过执行指令-直接生成当前jvm的dump文件" tabindex="-1"><a class="header-anchor" href="#人工通过执行指令-直接生成当前jvm的dump文件" aria-hidden="true">#</a> 人工通过执行指令，直接生成当前JVM的dump文件</h3>
<p>例：</p>
<ul>
<li>
<p>jmap -dump:format=b,file=/home/app/dump.hprof 21488，其中21488是JVM的当前进程号</p>
</li>
<li>
<p>内存或线程日志
jmap -dump:live,format=b,file=/home/myHeapDump.hprof 21488</p>
</li>
</ul>
<h2 id="二、分析dump" tabindex="-1"><a class="header-anchor" href="#二、分析dump" aria-hidden="true">#</a> 二、分析dump</h2>
<ol>
<li>打开JDK的 bin目录下jvisualvm.exe</li>
<li>在点击工具-&gt;插件里安装VisualVM-Classfish</li>
<li>导入.hprof文件</li>
</ol>
